@import url(https://fonts.googleapis.com/css?family=Karla&display=swap);
@import url(https://fonts.googleapis.com/css?family=Karla|Heebo&display=swap);
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Karla', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

words {
  font-size: 12px;
}
h1 {
  font-family: 'Karla';
  font-weight: 500;
  font-size: 2.5rem;  
  
}

#root {
  height: 100%;
}

img {
  max-width: 100%;
}

@media only screen and (max-width: 767px) {
  body, html {
    font-size: 14px;
  }

  h1 {
    font-size: 3.2rem;
  }
}

@-webkit-keyframes fade-in {
  from { opacity: 0 }
  to { opacity: inherit }
}

@keyframes fade-in {
  from { opacity: 0 }
  to { opacity: inherit }
}

@-webkit-keyframes fade-up {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    opacity: inherit;
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes fade-up {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    opacity: inherit;
    -webkit-transform: none;
            transform: none;
  }
}
.Navbar .logo {
  position: absolute;
  top: 30px;
  left: 45px;
  width: 125px;
  height: auto; }

.Navbar .logo:hover {
  -webkit-filter: brightness(95%);
          filter: brightness(95%); }

.Navbar .navlinks {
  position: absolute;
  top: 30px;
  right: 45px; }

.Navbar .navlink {
  cursor: pointer;
  font-size: 11.5px;
  margin-left: 40px;
  text-decoration: none;
  color: #1e272e;
  font-family: 'Karla';
  -webkit-transition: 0.1s color;
  transition: 0.1s color;
  text-transform: uppercase;
  letter-spacing: 1px; }

.Navbar .navlink:hover {
  color: #FCD962; }

.Navbar .selected {
  color: #FCD962; }

@media only screen and (max-width: 424px) {
  .Navbar .logo {
