.Illustrations {
  margin-top: 125px;
  -webkit-animation: fade-in 1.0s;
          animation: fade-in 1.0s;
  padding-bottom: 200px;
  width: 400px;
  max-width: 100%;
  text-align: center;
  font-size: 13px;
  margin: 0 auto;
  padding-top: 200px; }
  .Illustrations .image {
    margin: 50px auto;
    -webkit-animation: fade-in 1.0s;
            animation: fade-in 1.0s;
    width: 400px;
    max-width: calc(100%-20px); }
  .Illustrations .spin {
    -webkit-animation: image-spin infinite 20s linear;
            animation: image-spin infinite 20s linear; }

@-webkit-keyframes image-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes image-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }
  @media only screen and (max-width: 767px) {
    .Illustrations .image {
      max-width: 100%;
      height: auto; } }
  .Illustrations .title {
    padding-top: 0px;
    letter-spacing: 2px;
    font-size: 28px;
    margin-top: 80px;
    text-transform: uppercase;
    font-weight: 600;
    margin-top: 0;
    letter-spacing: 0.8px;
    color: #FCD962; }

